<template>
    <div class="container-fluid">
        <p class="my-3"></P>
        <div class="row justify-content-md-center">
            <div class="col-md-8 col-sm12">
                <div class="bg-white rounded-lg">
                    <label class="my-2 text-uppercase text-monospace">welcome to cobweb!</label>
                </div>
            </div>
        </div>
        <p class="my-3"></P>
        <div class="row justify-content-md-center">
            <div class="col-md-8 col-sm12">
                <div class="row">
                    <div :key="item.id" class="col-6" v-for="item in items">
                        <div class="card bg-white mb-3">
                            <div class="card-header">{{item.date}} - <span class="font-weight-bold">{{item.word}}</span></div>
                            <div class="card-body">
                                <img :src="item.path" class="card-img-bottom rounded-lg">
                                <p :key="index+1" class="mt-2 mb-0 card-text text-left" v-for="(text, index) in item.text">{{index+1}}. {{text}}</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name: 'Main',
  data () {
    return {
      items: [
        {
          'id': this.id(),
          'date': 'July 1st, 2019',
          'path': '/static/images/2019/07/01.png',
          'text': [
            'You just barge in here and you don\'t knock?',
            'I hope you don\'t mind me barging in like this.',
            'He barged in on us while we were having a meeting.'
          ],
          'word': 'barge'
        },
        {
          'id': this.id(),
          'date': 'July 2nd, 2019',
          'path': '/static/images/2019/07/02.png',
          'text': [
            'Don\'t you remember why you dumped the guy?',
            'The dead body was just dumped by the roadside.',
            'He\'s got no right to keep dumping his problems on me.',
            'Just dump your stuff over there - We\'ll sort it out later.',
            'Did you hear he\'s dumped his girlfriend?'
          ],
          'word': 'dump'
        },
        {
          'id': this.id(),
          'date': 'July 3rd, 2019',
          'path': '/static/images/2019/07/03.png',
          'text': [
            '... and they\'re both kind of insecure in bed, so ...',
            'He\'s very insecure about his appearance.',
            'She felt nervous and insecure.',
            'Jobs nowadays are much more insecure than they were ten years ago.',
            'As an artist he was always financially insecure.',
            'Insecure doors and windows make life easy for burglars.'
          ],
          'word': 'insecure'
        }
      ]
    }
  },
  methods: {
    id: function (length) {
      length = (length === undefined) ? 10 : length
      return Number(Math.random().toString().substr(3, length) + Date.now()).toString(36)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



